function getSteps() {
  return ['### 2023 Q1', '### 2023 Q2', '### 2023 Q3', '### 2023 Q4'];
}

function getStepContent(step: number) {
  switch (step) {
    case 0:
      return `
* ElfinGuard white paper release
* ElfinGuard official website release
* Opensource ElfinGuard's minimal environment
* Integrate the first CDN and Authorizer
* Application demo release
* Security audit`
      ;
    case 1:
      return `
* Storage supports Filecoin, Arweave, Swarm, etc.
* Integrate and promote more than 3 CDNs and Authorizer
* CDN and Authorizer reward and punishment mechanism
* CDN and Authorizer monitoring mechanism
* Economic model definition and implementation
      `;
    case 2:
      return `Try out different ad text to see what brings in the most customers,
              and learn how to enhance your ads using features like ad extensions.
              If you run into any problems with your ads, find out how to tell if
              they're running and how to resolve approval issues.`;
    default:
      return 'Unknown step';
  }
}
